import React from 'react';
import data from '../data/data';

const Affichages = (props) => {
    switch(props.page) {
        case "experiences":
            return  <div className="experiences">
                        <ul>
                            {data.experiences.map( ( job, index ) => <li key={index}><p className="titre">{job.titre}</p><p className="date">{job.date}</p><p className="employeur">{job.employeur}</p><p className="descriptif">{job.descriptif}</p></li> )}
                        </ul>
                    </div>;
        case "formations":
            return  <div className="formations">
                        <ul>
                            {data.formations.map( (formation, index ) => <li key={index}><p className="titre">{formation.titre}</p><p className="date">{formation.date}</p><p className="organisme">{formation.organisme}</p></li>)}
                        </ul>
                    </div>;
        case "competences":
            return  <div className="competences">
                        <ul className="technologie">Technologie:
                            {data.competences.technologie.map( (techno, index)=><li key={index}>{techno}</li> ) }
                        </ul>
                        <ul className="outils">Outils:
                            {data.competences.outils.map( (outil, index)=><li key={index}>{outil}</li> ) }
                        </ul>
                    </div>;
        default:
            throw new Error("Page innexistante")
    }
};

export default Affichages;