import React from 'react';

const Navigation = ( props ) => {
    return (
        <div className="navigation">
            <ul className="navContent">
                <li className="competences" onClick={()=> props.handleNavClick("competences")}>
                    Compétences
                </li>
                <li className="experiences" onClick={()=> props.handleNavClick("experiences")}>
                    Expériences
                </li>
                <li className="formations" onClick={()=> props.handleNavClick("formations")}>
                    Formations
                </li>
            </ul>
        </div>
    );
};

export default Navigation;