import React, { useState } from 'react';
import Affichages from '../Components/Affichages';
import Header from '../Components/Header';
import Navigation from '../Components/Navigation';



const Cv = () => {

    const [page, setPage] = useState("competences")

    const handleNavClick = (val) => {
        setPage(val)
    }

    return (
        <div className="content">
                <Header />
                <Navigation handleNavClick={handleNavClick}/>
                <Affichages page={page}/>
        </div>
    );
};

export default Cv;