import React from 'react';
import data from '../data/data';


const Header = () => {
    return (
        <div className="header">
            <div className="title">
                <h2>{data.informations.prenom} {data.informations.nom}</h2>
                <h3>{data.informations.poste}</h3>
            </div>
            <div className="info">
                <p>{data.informations.adresse}</p>
                <p>{data.informations.email}</p>
                <p>{data.informations.permis}</p>
            </div>
        </div>
    );
};

export default Header;